import func from './util';
import emptyImage from '../assets/empty-photo.png';
import { IMG_URL } from '../http/url';

const misc = {
  accessToken() {
    const admin = func.getItem('admin', true);
    if (admin) {
      const { accessToken } = admin;
      return accessToken;
    }
    return '';
  },

  registrationId() {
    const data = func.getItem('admin', true);
    if (data) {
      return data.profile.registrationId;
    }
    return '';
  },

  profile() {
    const data = func.getItem('admin', true);
    if (data) {
      return data.profile;
    }

    return '';
  },

  userImage(userProfile) {
    const UserImage = userProfile === null ? emptyImage : IMG_URL + userProfile;
    return UserImage;
  },
};

export default misc;
